<template lang="pug">
  // li(ref="main", :style="styles")
  .project(ref="main", style="styles")
    .row
      a.description(:href="project.link", target="_blank")
        .words
          h3 {{project.title}}
          p {{project.description}}
          img(:src="project.image")
      .stack
        h3 STACK
        ul
          li(v-for="tech in project.stack")
            p {{ tech }}
            
</template>

<script>
  export default {
    props:{
      project:Object,
      index:Number,
    },
    data(){
      return {
        scrollY:0,
      }
    },
    computed:{
      styles(){
        let x = (this.scrollOrigin - this.scrollY)
        let r = 100;
        let a = 0;
        let b = 0;
        let y = Math.sqrt(r**2 - (x-a)**2) + b
        let offset = y.toFixed(2);
        return {
          // transform:`scale(${offset})`
        }
      },
      scrollOrigin(){
        let el = this.$refs.main
        this.scrollY = el? el.offsetTop : 0;
        return this.$parent.scrollOrigin
      },

    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/css/global";

  .smooth{
    // transition:transform 100ms linear;
  }
  .words{

  }
  @include mobile{
    .project{
      border-bottom:2px solid white;
    }
  }
  .project{
    margin:1rem;
    margin-top:7.5%;
    color:white;
    width:100%;
    z-index: 2;
    overflow: hidden;

    // border-bottom:2px solid black;
    // box-shadow: 0 10px 15px rgba($colorE, 0.8);
    .description{
      transition:transform 500ms ease-in;
      color:white;
      width:50%;
      @include mobile{
        width:100%;
        float:none;
      }
      float:left;
      // border-right:1px solid white;
      margin-right:3px;
      text-shadow: 0px -1px 10px rgba($colorE, 0.8);
      .words{
        margin:0 3rem;
        z-index:2;
        overflow:hidden;
        p{
        }
      }
      img{
        height:10rem;
        transition: box-shadow 250ms ease-in;
        box-shadow: 0 0 4px rgba($colorE, 0.5);
      }

    &:hover{
      text-shadow: 0px -1px 15px rgba($colorE, 0.9);
      text-decoration: none;
      img{
        box-shadow: 0 0 15px rgba($colorE, 0.7);
      }
    }
    }
    .stack{
      width:49%;
      float:left;
      text-align: left;
      h3{
        text-align: center;
        // text-decoration: underline;
      }
      ul{
        margin-left:10%;
        font-family: 'Quicksand', cursive;
        text-shadow: 1px 1px 5px rgba($colorE, 0.5);
        li{
          list-style: none;
          z-index:2;
          overflow:hidden;
        }
      }
      @include iphone6{
        width:100%;
        float:none;
        ul{
          text-align: center;
          margin-left:0;
          li{
            text-align: center;
          }
        }
      }
      // margin-left:30rem;
    }
  }
</style>


