<template lang="pug">
  .row.home
    .curve
      sine(v-for="(a,i) in Array.from({length:30})")
    .col-xs-12
      .row
        .col-xs-12
          header
            h1 McCall Alexander
            h2 Web Development & Design
            address Boise, Idaho
            nav
              ul
                li
                  a(href="https://www.instagram.com/mccallalexander/", target="_blank")
                    i.fa.fa-instagram.fa-2x
                li
                  a(href="https://github.com/mccallofthewild", target="_blank")
                    i.fa.fa-github.fa-2x
                li
                  a(href="https://twitter.com/mccallthetall", target="_blank")
                    i.fa.fa-twitter.fa-2x
          ShootingStar(v-for="(a,i) in Array.from({length:30})")
          .sun-container
            sun
          .mountains
            mountain.left
            mountain.right
        .col-xs-12
          .projects.row
            .scroll-prompt
              i.material-icons arrow_downward
              p PROJECTS
            // project(v-for="(project, index) in Array.from({length:20})", :index="index", :project="project")
            project(v-for="(project, index) in projects", :key="index", :index="index", :project="project")

</template>

<script>
  import Project from './Project'
  import projects from './projects'
  import Sine from './Sine'
  import Mountain from './Mountain'
  import Sun from './Sun'
  import ShootingStar from './ShootingStar'

  export default {
    name: 'hello',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        scrollOrigin:0,
      }
    },
    computed:{
      projects(){
        return projects;
        return projects.sort((a,b)=>{
          return a.weight - b.weight
        }, {weight:0})
      }
    },
    components:{
      Project,
      Sine,
      Mountain,
      Sun,
      ShootingStar,
    },
    methods:{
      handleScroll(){
        let winHeight = window.innerHeight
        let centerScrollPos = window.scrollY - winHeight/2
        this.scrollOrigin = centerScrollPos

      }
    },
    mounted(){
      console.log(projects)
      window.addEventListener('scroll', this.handleScroll);
      this.handleScroll()
    },
    beforeDestroy () {
      window.removeEventListener('scroll', this.handleScroll);
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import '../assets/css/media-queries';
  @import '../assets/css/variables';

  @keyframes fadeInOut{
    from{
      opacity:0.8;
    }
    to{
      opacity:0;
    }
  }

  .sunset{
    background-image:url(../assets/nightsky.jpg), $sunset;
    background-blend-mode:soft-light;
    background-size: cover;
    background-attachment: fixed;
  }
  .home{
    position:relative;
    z-index:2;
    @extend .sunset;
    overflow:hidden;
    color:azure;
    // height:75vh;
    text-align: center;
    font-family: 'Bungee Hairline', cursive;
// font-family: 'Josefin Sans', sans-serif;
    font-family: 'Quicksand', sans-serif;
    // text-shadow: 0px -1px 10px rgba($colorE, 0.8);
    font-size:1.5rem;
    header{
      text-transform: uppercase;
      height:90vh;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      h1{
        letter-spacing: 1.5rem;
      }
      h2{
        letter-spacing: 0.85rem;
      }
      address{
        text-align: center;
        font-style: normal;
        letter-spacing: 1.5rem;
      }
    }
    nav{
      ul{
        padding:0;
        li{
          display: inline-block;
          list-style: none;
          margin:0 3.5em;
          text-shadow: 0px -1px 10px rgba($colorE, 0.5);
          z-index:2;
          overflow:hidden;
          a{
            color:azure;
            font-size: 1rem;
          }
        }
      }
    }
    .sun-container{
      display: none !important;
      position:absolute;
      width: 100%;
      bottom:0;
      left: 0;
      right: 0;
      display: flex;
      justify-content: center;
      align-items:center;
      margin-bottom:40%;
      
    }
    .mountains{
      display: none;
      justify-content: space-around;
      align-items: center;
      .left{
        transform:scale(0.85) translate(100px, -45px);
      }
      .right{
        transform:rotate(-5deg);

      }
    }
  }
  .projects{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .scroll-prompt{
      display: flex;
      justify-content: center;
      align-items: center;
      font-size:1.5rem;
      p{
      }
      i{
        font-size:1.3em;
      }
      text-shadow: 0px -1px 10px rgba($colorE, 0.3);
      animation: fadeInOut 2s ease-out infinite alternate;
    }
  }
  .curve{
    position: fixed;
    top:10%;
    height:100%;
    right:45%;
    z-index:0;
  }
</style>


